header {
  background-image: url("../images/header.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  .real-header {
    transform: translateY(-50%) translateX(-50%);
    top: 50%;
    width: 85%;
    left: 50%;

    svg.bi-heart-fill {
      color: $main-bg;
      animation: pulse 1.7s ease-in-out infinite;
      // color: #e31b23;
      transition: all ease-in-out 0.7s;
    }
  }

  /* ---------- angle-down ---------*/
  .angle-down {
    right: 49%;
    bottom: 30px;
    transition: all 0.5s ease-in-out;
    animation: top-bottom 2s infinite alternate-reverse;
  }
  @keyframes top-bottom {
    0% {
      bottom: 20px;
    }
    // 25% {
    //   bottom: 30px;
    // }
    50% {
      bottom: 35px;
    }
    // 75% {
    //   bottom: 30px;
    // }
    100% {
      bottom: 20px;
    }
  }
}

.upcoming-events {
  position: relative;
  &::before {
    content: "";
    display: block;
    width: 80%;
    height: 3px;
    background-color: #f2f2f2;
    top: 50%;
    position: absolute;
    z-index: -1;
    right: 30px;
  }
}

.offers {
  .event-time {
    width: 200px;
    border: #fff 2px solid;
    position: absolute;
    text-align: center;
    top: 20px;
    right: -50px;
    transform: rotate(45deg);
    font-weight: bold;

    .ribbon-wrapper {
      position: relative;
      z-index: 10;
    }
    .ribbon-front {
      padding: 15px;
      height: 100%;
      width: 100%;
      position: relative;
      z-index: 2;
      color: #fff;
      text-align: center;
    }
    .glow {
      width: 40px;
      height: 100%;
      z-index: 999;
      position: absolute;
      animation: flow 3s linear infinite;
      transform: skew(45deg);
      background: linear-gradient(
        to right,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0) 1%,
        rgba(255, 255, 255, 1) 100%
      );
      -webkit-animation: flow 3s linear infinite;
      -webkit-transform: skew(45deg);
      -moz-transform: skew(45deg);
      -ms-transform: skew(45deg);
      -o-transform: skew(45deg);
    }
  }
  @keyframes flow {
    0% {
      left: -20%;
      opacity: 0;
    }

    50% {
      left: 50%;
      opacity: 0.5;
    }

    100% {
      left: 100%;
      opacity: 0;
    }
  }
}



.about-me {
  .img img {
    animation: border-transform 10s linear infinite alternate forwards;
  }
}

.tracks {
  overflow: hidden;

  .background-text {
    &::before {
      content: "Music Podcast";
    }
  }

  .main-content {
    height: 500px;
    overflow-y: auto;
  }
  .bar-item {
    background-color: $main-bg;
  }
  .bar-slider .bar-slider-btn {
    background: $main-bg;
    border: 0;
    border-radius: 50%;
    height: 0.6rem;
    padding: 0;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(50%, -50%);
    width: 0.6rem;
  }

  .slider {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: transparent;
    display: block;
    height: 1.2rem;
    width: 100%;

    &:focus {
      // box-shadow: 0 0 0 .1rem rgba(87, 85, 217, .2);
      outline: none;
    }

    &tooltip:not([data-tooltip])::after {
      content: attr(value);
    }

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      background: $main-bg;
      border: 0;
      border-radius: 50%;
      height: 0.6rem;
      margin-top: -0.25rem;
      -webkit-transition: transform 0.2s;
      transition: transform 0.2s;
      width: 0.6rem;
    }

    &::-moz-range-thumb {
      background: $main-bg;
      border: 0;
      border-radius: 50%;
      height: 0.6rem;
      -moz-transition: transform 0.2s;
      transition: transform 0.2s;
      width: 0.6rem;
    }

    &::-ms-thumb {
      background: $main-bg;
      border: 0;
      border-radius: 50%;
      height: 0.6rem;
      -ms-transition: transform 0.2s;
      transition: transform 0.2s;
      width: 0.6rem;
    }

    &:active::-webkit-slider-thumb {
      transform: scale(1.25);
    }

    &:active::-moz-range-thumb {
      transform: scale(1.25);
    }

    &:active::-ms-thumb {
      transform: scale(1.25);
    }

    &:disabled::-webkit-slider-thumb,
    &disabled::-webkit-slider-thumb {
      background: #f7f8f9;
      transform: scale(1);
    }

    &:disabled::-moz-range-thumb,
    &disabled::-moz-range-thumb {
      background: #f7f8f9;
      transform: scale(1);
    }

    &:disabled::-ms-thumb,
    &disabled::-ms-thumb {
      background: #f7f8f9;
      transform: scale(1);
    }

    &::-webkit-slider-runnable-track {
      background: #eef0f3;
      border-radius: 0.1rem;
      height: 0.1rem;
      width: 100%;
    }

    &::-moz-range-track {
      background: #eef0f3;
      border-radius: 0.1rem;
      height: 0.1rem;
      width: 100%;
    }

    &::-ms-track {
      background: #eef0f3;
      border-radius: 0.1rem;
      height: 0.1rem;
      width: 100%;
    }

    &::-ms-fill-lower {
      background: $main-bg;
    }
  }

  .controller-container {
    width: 70px;
    height: 70px;
    overflow: hidden;
    position: relative;
    border-radius: 50%;
    background-color: #f2f2f2;

    .pause,
    .play {
      width: 100%;
      height: 100%;
      cursor: pointer;
      position: absolute;
    }
    .line {
      position: absolute;
      width: 8px;
      height: 50%;
      background: $main-bg;
      border-radius: 10px;
      transition: all 400ms cubic-bezier(0.8, 0, 0.33, 1);
    }
    .pause {
      .line {
        &_1 {
          margin: 25% 0;
          left: 28%;
        }
        &_2 {
          margin: 100% -16%;
          right: 45%;
          transition-delay: 200ms;
        }
      }

      &.active {
        .line {
          opacity: 1;
          &_1 {
            margin: 25% 0;
            left: 28%;
          }
          &_2 {
            margin: 25% 0;
            right: 28%;
          }
        }
      }
    }
    .play {
      .line {
        margin: 25% 0;
        &_1 {
          left: 28%;
        }
        &_2 {
          height: 56%;
          left: 60%;
          transform: rotate(-55deg) translateY(-128px) translateX(16px);
          transition-delay: 100ms;
        }
        &_3 {
          height: 56%;
          left: 60%;
          transform: rotate(55deg) translateY(-128px) translateX(16px);
          transition-delay: 200ms;
        }
      }
      &.active {
        .line {
          opacity: 1;
          height: 62%;
          margin: 20% 0;
          &_1 {
            left: 28%;
            transform: translateY(-2px);
          }
          &_2 {
            height: 56%;
            left: 64%;
            transform: rotate(-55deg) translateY(-14px) translateX(1px);
          }
          &_3 {
            height: 56%;
            left: 66%;
            transform: rotate(58deg) translateY(16px) translateX(1px);
          }
        }
      }
    }
  }
}

.works .lastWork {
  background-image: url("../images/color-full.jpg");
  background-position: center;
  background-size: cover;
  height: 500px;
  .overlay {
    background-color: #f1f1f13d;
  }
  .player {
    top: 50%;
    background-color: $main-bg;
    .icon {
      color: #fff;
    }
  }
}

.works .oldWorks .work:nth-child(1),
.works .oldWorks .work:nth-child(4) {
  background-color: #f2f2f2;
  height: 250px;
}

.youtube {
  .work {
    transition: all 0.5s ease-in-out;
    .overlay {
      background: #f1f1f166;
    }
  }

  .work .player {
    position: absolute;
    left: 0;
    bottom: 50%;
    right: 0;
    border-color: #fff;
    background-color: $main-bg;
    .icon {
      color: #fff;
    }
  }
  .work:hover {
    box-shadow: 0px 3px 15px rgba(22, 41, 124, 0.1);
  }
}

.timeEvent {
  background-color: #f2f2f2;
  .contain {
    color: #333;
    margin: 0 auto;
    padding: 0.5rem;

    li {
      display: inline-block;
      font-size: 1.5em;
      list-style-type: none;
      padding: 1em;
      text-transform: uppercase;
    }
  }
}
